@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,600;1,400;1,600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  font-family: 'Roboto', sans-serif;
}

html, body {
  @apply h-screen w-full bg-slate-900 flex-col justify-center items-center;
}

@layer components {
  .btn-socks {
    @apply inline-flex items-center justify-center p-2 bg-sky-500 hover:bg-sky-400 rounded-sm shadow-lg text-slate-100 text-xs;
  }
}